import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { from, Observable, Subscriber } from 'rxjs';
import { AccessProviders } from '../../providers/access-providers';
import { ToastController, LoadingController, AlertController } from '@ionic/angular';


//registro semana
import { semana } from '../modelo/semana';

//calendario
import { CalendarComponentOptions } from 'ion2-calendar'



@Component({
  selector: 'app-menu-valle',
  templateUrl: './menu-valle.page.html',
  styleUrls: ['./menu-valle.page.scss'],
})
export class MenuVallePage implements OnInit {
  //calendario
  dateMulti: string[];
  type: 'string';

  optionsMulti: CalendarComponentOptions = {
    pickMode: 'multi'
  };

  //del registro de la semana

  objeto:semana = {numsemana:"", descripcion:"", lunes:"", martes:"", miercoles:"", jueves:"", viernes:"", sabado:"", domingo:"" }
  
  semanas: semana[] = [];
  url2 = 'http://localhost/proG/server_api/menu_semanalv.php';
  url = 'http://localhost/proG/server_api/crear_semana.php';
 

  constructor(
    public http: HttpClient,
    private router: Router,
    private toastCtrl: ToastController,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    private accsPrvds: AccessProviders,
    private actRoute: ActivatedRoute 
  
    ){
    this.listarSemanas();
  }

  getSemanas(): Observable<any>{
    return this.http.get(this.url2);
  }

  listarSemanas(){
    this.semanas = [];
    this.getSemanas().subscribe(res => {
      for(let i=0; i<res.length; i++){
        this.semanas.push(res[i]);
      }
    })
  }

  agregarSemana(obj :semana): Observable<semana>{
    return this.http.post<semana>(this.url, JSON.stringify(obj))
  }

  async agregar() {
    this.agregarSemana(this.objeto)
    .subscribe((resp) =>{
      this.objeto.numsemana="";
      this.objeto.descripcion='';
      this.objeto.lunes='';
      this.objeto.martes='';
      this.objeto.miercoles='';
      this.objeto.jueves='';
      this.objeto.viernes='';
      this.objeto.sabado='';
      this.objeto.domingo='';
      this.listarSemanas();
      console.log(resp)
    })
    const toast = await this.toastCtrl.create({
      message: "Registro Exitoso",
      duration: 1500
    });
    toast.present();
  }


  ngOnInit() {
  }


  //calendario
  onChange($event) {
    console.log($event);
  }


  async delData(a){
    return new Promise(resolve => {
      let body = {
        aksi: 'eliminar_MSV',
        id: a
      }
      this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
        if(res.success==true){
          this.presentToast('Eliminado con exito');
          this.listarSemanas();
        }else{
          this.presentToast('delete error');
        }
      });

    }); 

  }

  async presentToast(a){
    const toast = await this.toastCtrl.create({
      message: a,
      duration: 1500
    });
    toast.present();
  }


  abrirCrud(a){
    this.router.navigate(['/mod-menuv/'+a]);
  }



}
